import React, {useEffect, useState} from "react";
import BodyTemplate from "../template/BodyTemplate";
import RouteTemplate from "../template/RouteTemplate";
import HotProduct from "../home/components/HotProduct";
import {useParams, useLocation, useHistory} from "react-router-dom"
import {getItems} from "../../api/api";
import LoadingCircle from "../template/LoadingCircle";
import ProductCard from "../template/ProductCard";
import './SearchProduct.css'
import Paginate from "../template/Paginate/Paginate";
import MetaTag from "../template/MetaTag/MetaTag";

export default function SearchProduct() {
    const location = useLocation()
    const url_params = new URLSearchParams(location.search)
    const query = url_params.get('query')
    const page = url_params.get('page')
    const history = useHistory()
    const [result, setResult] = useState(() => ({
        isLoading: true,
        page: page,
        item_per_page: 16,
        total: 0,
        total_page: 0,
        list: []
    }))
    useEffect(() => {
        setResult({
            ...result,
            isLoading: true
        })
        getItems(`/all_product?query=${query}&page=${page}&item_per_page=${result.item_per_page}&order=is_hot,desc`, {timeout: 20000})
            .then(res => setResult({
                ...result,
                isLoading: false,
                item_per_page: res.data.item_per_page,
                page: res.data.page,
                total: res.data.total,
                total_page: res.data.total_page,
                list: res.data.list
            }))
    }, [query, page])
    const chosePage = (newPage) => {
        window.scrollTo(0, 0)
        history.push(`/search?query=${query}&page=${newPage}`)
    }
    return (
        <BodyTemplate>
            <MetaTag title={`Tìm kiếm : ${query}`} />
            {
                result.isLoading ?
                    <div style={{display: 'flex', justifyContent: 'center'}}>
                        <LoadingCircle/>
                    </div> :
                    <React.Fragment>
                        <RouteTemplate data={[
                            {
                                name: 'Trang chủ',
                                path: '/',
                            },
                            {
                                name: 'Tìm kiếm'
                            }
                            ,
                        ]}/>
                        {/*<HotProductNav/>*/}
                        <h2>{`Có ${result.total} kết quả tìm kiếm phù hợp với "${query}"`}</h2>
                        <div className='search-product-container'>
                            {result.list.map((item, index) => {
                                return <div key={index} className='search-product-element'>
                                    <ProductCard data={item}/>
                                </div>
                            })}
                        </div>
                        <Paginate totalPage={result.total_page} currentPage={page} handlePage={chosePage}/>
                    </React.Fragment>
            }
        </BodyTemplate>
    );
}
